@import "../variables";
@import "../mixins";
/*======= Portfolio =======*/

.portfolios {
	@include flexbox;
	@include flex-wrap(wrap);
	margin-left: -2.5px;
	margin-right: -2.5px;

	.portfolio {
		width: 20%;

		.portfolio__inner {
			position: relative;
			margin: 0 2.5px;
			margin-bottom: 5px;

			.portfolio__image {
				position: relative;


				&:before {
					content: "";
					position: absolute;
					left: 0;
					top: 0;
					height: 100%;
					width: 100%;
					z-index: 2;
					@include gradient-bg(90);
					opacity: 0.5;
					visibility: hidden;
					opacity: 0;
					@include transition(all 0.3s ease-in-out 0s);
				}
			}

			.portfolio__img {
				object-fit: cover;
			}

			.portfolio__content {
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
				width: 100%;
				padding: 20px;
				z-index: 3;
				visibility: hidden;
				opacity: 0;
				@include transition(all 0.3s ease-in-out 0s);

				.portfolio-zoom-trigger {
					a {
						font-size: 36px;
						color: #ffffff;
						position: absolute;
						left: 50%;
						top: 50%;
						margin-top: -15px;
						visibility: hidden;
						opacity: 0;
						@include transition(all 0.3s ease-in-out 0.2s);
						@include transform(translate(-50%, -50%));

						&:hover {
							color: #ffffff;
							opacity: .9;
						}
					}
				}

				.portfolio__content__text {
					@include flexbox;
					@include justify-content(space-between);
					@include align-items(center);
					position: absolute;
					left: 20px;
					bottom: 0;
					visibility: hidden;
					opacity: 0;
					@include transition(all 0.3s ease-in-out 0.2s);
					width: calc(100% - 40px);

					h5 {
						margin-bottom: 0;
						padding-right: 15px;
						line-height: 21px;
						width: calc(100% - 75px);
					}

					a {
						letter-spacing: 0.1px;
						font-weight: 500;
						text-transform: uppercase;
						color: #ffffff;

						span {
							padding-right: 5px;
						}

						&:hover {
							color: #ffffff;
						}
					}

					.share__content__wrap {
						text-align: right;
						width: 75px;

						.share__content {
							right: 0;
							left: auto;
							@include transform(translateX(0));

							&:before {
								left: auto;
								right: 25px;
							}
						}
					}
				}
			}

			&:hover {
				.portfolio__image {
					&:before {
						visibility: visible;
						opacity: 0.5;
					}
				}

				.portfolio__content {
					visibility: visible;
					opacity: 1;

					.portfolio-zoom-trigger {
						a {
							visibility: visible;
							opacity: 1;
							margin-top: 0;
						}
					}

					.portfolio__content__text {
						visibility: visible;
						opacity: 1;
						bottom: 20px;
					}
				}
			}
		}
	}
}




// Extra Large Layout
@media #{$xx-layout} {
	.portfolios {
		.portfolio {
			width: 20%;
		}
	}
}


// Extra Large Layout
@media #{$xl-layout} {
	.portfolios {
		.portfolio {
			width: 25%;
		}
	}
}


// Large Layout
@media #{$lg-layout} {
	.portfolios {
		.portfolio {
			width: 33.33%;

			&:last-child {
				display: none;
			}
		}
	}
}



// Medium Layout
@media #{$md-layout} {
	.portfolios {
		.portfolio {
			width: 50%;

			.portfolio__image {
				img {
					width: 100%;
				}
			}
		}
	}
}


// Small Layout
@media #{$sm-layout} {
	.portfolios {
		.portfolio {
			width: 70%;
			margin-left: 15%;

			.portfolio__image {
				img {
					width: 100%;
				}
			}

			.portfolio__inner {
				margin-bottom: 30px;
			}

			&:last-child {
				.portfolio__inner {
					margin-bottom: 0;
				}
			}
		}
	}
}

// Extra small Layout
@media #{$xs-layout} {
	.portfolios {
		.portfolio {
			width: 100%;
			margin-left: 0;
		}
	}
}




// Portfolio layout-2 
.portfolio-filter {
	margin-bottom: 70px;

	button {
		display: inline-block;
		outline: none;
		border: none;
		color: #353535;
		margin: 0 15px;
		padding: 0 5px;
		text-transform: uppercase;
		background: transparent;
		background-color: transparent;
		letter-spacing: 1px;
		cursor: pointer;
		height: 22px;
		@include transition(all 0.3s ease-in-out 0s);
		position: relative;

		span {
			position: relative;
		}

		&:before {
			content: "";
			position: absolute;
			left: 0;
			top: 50%;
			height: 50%;
			background: $theme-color;
			width: 100%;
			@include transform(scaleX(0));
			@include transition(all 0.3s ease-in-out 0s);
		}

		&.is-checked {
			&:before {
				@include transform(scaleX(1));
			}
		}

		&:hover {
			opacity: 0.8;
		}

		&:focus,
		&:active {
			outline: none;
			border: none;
		}
	}
}

.portfolios.layout-2 {
	margin-left: -15px;
	margin-right: -15px;

	.portfolio {
		width: 33.33%;

		.portfolio__inner {
			position: relative;
			margin: 0 15px;
			margin-bottom: 30px;

			.portfolio__content {
				padding: 30px;

				.portfolio__content__text {
					left: 30px;
					width: calc(100% - 60px);
				}
			}

			&:hover {
				.portfolio__content {
					.portfolio__content__text {
						bottom: 30px;
					}
				}
			}
		}
	}
}


// Medium lyout
@media #{$md-layout} {
	.portfolio-filter {
		button {
			margin: 0 12px;
		}
	}

	.portfolios.layout-2 {
		.portfolio {
			width: 50%;
		}
	}
}

// Medium lyout
@media #{$sm-layout} {
	.portfolio-filter {
		text-align: center;

		button {
			display: block;
			width: 100%;
			margin: 10px 0;

			span {
				display: block;
				width: 100%;
			}
		}
	}

	.portfolios.layout-2 {
		.portfolio {
			width: calc(100% - 60px);
			margin-left: 30px;
			margin-right: 30px;

			&:last-child {
				.portfolio__inner {
					margin-bottom: 0;
				}
			}
		}
	}
}

// Medium lyout
@media #{$xs-layout} {
	.portfolios.layout-2 {
		.portfolio {
			width: 100%;
			margin-left: 0px;
			margin-right: 0px;
		}
	}
}








// Portfolio layout 3
.gallery-portfolios {
	.portfolio {
		float: left;
		width: 50%;
		overflow: hidden;
		@include transition(inherit);

		.portfolio__inner {
			position: relative;

			.portfolio__image {
				cursor: pointer;

				img {
					max-width: 100%;
				}
			}

			.portfolio__content {
				position: absolute;
				left: 0;
				bottom: 0;
				width: 100%;
				padding: 25px 30px;
				visibility: hidden;
				opacity: 0;
				@include transform(translateY(100%));
				@include transform-origin(50% 100%);
				@include transition(all 0.3s ease-in-out 0s);

				h5,
				h6 {
					position: relative;
					display: block;
					width: 100%;
					margin-bottom: 0;
					color: #ffffff;
					z-index: 2;
					letter-spacing: 0.1px;
				}

				h5 {
					font-size: 16px;
					font-weight: 500;
					line-height: 22px;

					a {
						color: #ffffff;

						&:hover {
							opacity: 0.9;
						}
					}
				}

				h6 {
					font-size: 14px;
					font-weight: 400;
				}

				&:before {
					content: "";
					position: absolute;
					height: 100%;
					width: 100%;
					opacity: .5;
					left: 0;
					bottom: 0;
					@include gradient-bg(90);
					z-index: 1;
				}
			}
		}

		&:hover {
			.portfolio__inner {
				.portfolio__content {
					opacity: 1;
					visibility: visible;
					@include transform(translateY(0));
				}
			}
		}
	}
}

// Small layout
@media #{$sm-layout} {
	.gallery-portfolios {
		.portfolio {
			width: 100%;
		}
	}
}